import React from 'react';
import './Analysis.css';
import { Link, Route, Switch } from 'react-router-dom';
import Navigation from '../Navigation/Navigation';

class Customers extends React.Component{

    constructor({props})
    {
        super(props);
        this.c=0;
        this.dayString='';
        this.state={
            'totalForTheDay':[],
            'totalForTheMonth':[],
            'totalForTheWeek':[],
            'allItems':[],
            'avgByDay':[],
            'avgByMonth':[],
            'avgByWeek':[],
            'avgByDay':[]
        }

    }

    DateprototypegetWeek=()=>{ 
    var determinedate = new Date(); 
    determinedate.setFullYear(determinedate.getFullYear(), determinedate.getMonth(), determinedate.getDate()); 
    var D = determinedate.getDay(); 
    if(D == 0) D = 7; 
    determinedate.setDate(determinedate.getDate() + (4 - D)); 
    var YN = determinedate.getFullYear(); 
    var ZBDoCY = Math.floor((determinedate.getTime() - new Date(YN, 0, 1, -6)) / 86400000); 
    var WN = 1 + Math.floor(ZBDoCY / 7); 
    return WN; 
    }

    componentDidMount()
    {
        var week=this.DateprototypegetWeek();
        var tempDate = new Date();
        var day=tempDate.getDay();
        var corDate=tempDate.getDate()+'-'+(tempDate.getMonth()+1)+'-'+tempDate.getFullYear();
        var month=tempDate.getMonth()+1;
        var year=tempDate.getFullYear();
        
        console.log("weeel---",week);
        if(day==1)
            this.dayString="Mondays";
        else if(day==2)
            this.dayString="Tuesdays";
        else if(day==3)
            this.dayString="Wednesdays";
        else if(day==4)
            this.dayString="Thursdays";
        else if(day==5)
            this.dayString="Fridays";
        else if(day==6)
            this.dayString="Saturdays";
        else if(day==7)
            this.dayString="Sundays";


        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/customer/total/current/day',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id,
                meal_date:corDate
            })
        })
        .then(response=>response.json())
        .then(results=>{
            //var temp=Math.round(results[0].avg*100)/100;
            this.setState({'totalForTheDay':results[0].count})
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/customer/total/current/month',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id,
                month:month,
                year:year
            })
        })
        .then(response=>response.json())
        .then(results=>{
            //var temp=Math.round(results[0].avg*100)/100;
            this.setState({'totalForTheMonth':results[0].count})
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/customer/total/current/week',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id,
                month:month,
                year:year,
                week:week
            })
        })
        .then(response=>response.json())
        .then(results=>{
            //var temp=Math.round(results[0].avg*100)/100;
            this.setState({'totalForTheWeek':results[0].count})
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/average/customer/monthly',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id
            })
        })
        .then(response=>response.json())
        .then(results=>{
            var temp=Math.round(results[0].avg*1)/1;
            this.setState({'avgByMonth':temp})
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/average/customer/weekly',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id
            })
        })
        .then(response=>response.json())
        .then(results=>{
            var temp=Math.round(results[0].avg*1)/1;
            this.setState({'avgByWeek':temp})
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/average/customer/daily',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id
            })
        })
        .then(response=>response.json())
        .then(results=>{
            var temp=Math.round(results[0].avg*1)/1;
            this.setState({'avgByDay':temp})
        })

    }


    render(){
        return(
            <div>
            <Navigation email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
                <div className="headers">
                <p className="analysisT">Customer Analysis.</p>
                <br />
                <hr />
                <p className="totalT">Customer Count-</p>
                <br />
                <table>
                    <tbody>
                        <tr>
                        <th></th>
                        <th>Today</th>
                        <th>This Week</th>
                        <th>This Month</th>
                        </tr>
                        
                        <tr>
                        <th>Total</th>
                        <td>{this.state.totalForTheDay}</td>
                        <td>{this.state.totalForTheWeek}</td>
                        <td>{this.state.totalForTheMonth}</td>
                        </tr>
                        </tbody>
                </table>

                <br/>
                <p className="totalT">Average Count-</p>
                <br />
                <table>
                    <tbody>
                        <tr>
                        <th></th>
                        <th>Day</th>
                        <th>Week</th>
                        <th>Month</th>
                        </tr>
                        
                        <tr>
                        <th>Total</th>
                        <td>{this.state.avgByDay}</td>
                        <td>{this.state.avgByWeek}</td>
                        <td>{this.state.avgByMonth}</td>
                        </tr>
                        </tbody>
                </table>

            </div>
            <Link to={"/home/analysis/"+this.props.match.params.email+"/"+this.props.match.params.rest_id}><button className="button1"><i class="fa fa-arrow-left"></i> Back</button></Link>
            <br/>
            <br/>
            <br/>
            </div>
            )
            
        }
    }
export default Customers;


