import React from 'react';
import './Cards.css';
import { Link, Route, Switch } from 'react-router-dom';
import Navigation from '../Navigation/Navigation';

class Cards extends React.Component{
	
	
	constructor({props,rest_id})
	{
		super(props);
		this.list;
		this.phone=0;
		this.corDate;
		this.state={
			'details':[],
			addCustomer:'noName',
			search:'',
			addNumber:0,
			cust_id:0,
			validCheck:"Adding Without Number",
			sms:0
		}
	}
	
	 MyFunction(){
  		var tempDate = new Date();
  		//var date = tempDate.getFullYear() + '-' + (tempDate.getMonth()+1) + '-' + tempDate.getDate() +' '+ tempDate.getHours()+':'+ tempDate.getMinutes()+':'+ tempDate.getSeconds();
  		var time=tempDate.getHours()+':'+ tempDate.getMinutes();
  		var corDate=tempDate.getDate()+'-'+(tempDate.getMonth()+1)+'-'+tempDate.getFullYear();
  		this.corDate=corDate;
  		//const currDate = "Current Date= "+date;
  		console.log('DAtetetetete----',time);
	}


	onNameChange =(event)=>
	{
		this.setState({addCustomer: event.target.value})
	}

	onNumberChange =(event)=>
	{
		this.phone=event.target.value;
		//this.setState({addNumber: event.target.value})
		if((this.phone.length>0 && this.phone.length<10) || (this.phone.length>10))
			this.setState({validCheck: "Incorrect Phone Number"})
		if(this.phone.length==10)
			this.setState({validCheck: "Verified"})

		console.log("NUm",this.state.addNumber.length+1);
	}

	onItemChange = (event) => {
		this.setState({search : event.target.value})
		console.log(this.state.search);
	}

	onSubmitName=()=>{
		var tempDate = new Date();
  		//var date = tempDate.getFullYear() + '-' + (tempDate.getMonth()+1) + '-' + tempDate.getDate() +' '+ tempDate.getHours()+':'+ tempDate.getMinutes()+':'+ tempDate.getSeconds();
  		var corDate=tempDate.getDate()+'-'+(tempDate.getMonth()+1)+'-'+tempDate.getFullYear();
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/newcustomer',{
			method:'post',
			headers:{'Content-Type': 'application/json'},
			body: JSON.stringify({
				name:this.state.addCustomer,
				rest_id:this.props.match.params.rest_id,
				meal_date:corDate
			})
		})
		.then(response=>response.json())
		.then(results=>{
			console.log("REsultsssss------------",results)
			var test=this.state.details.concat(results);
			console.log("REsultsssss------------",test)
			this.setState({'details':test})

			if(this.phone.length==10 && this.state.sms>0)
			{
				fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getRestName',{
					method:'post',
					headers:{'Content-Type': 'application/json'},
					body: JSON.stringify({
						rest_id:this.props.match.params.rest_id
					})
				})
				.then(response=>response.json())
				.then(res=>{
					console.log("iN SEMSMS",res)
					var Sms=this.state.sms-1;
					this.setState({sms:Sms})

					console.log("iN SEMSMS",results)
					//var len=results.length-1;
					var custId=results[0].cust_id;
					var custName=results[0].name;
					console.log("SMSMSS---",custId,custName);
					var msg="Check your BillMaster E-Bill for "+res[0].rest_name+" on "+this.corDate+" at -> http://www.billmaster.xyz/customer/bill/"+this.props.match.params.rest_id+"/"+custId+"/"+custName+"";
					console.log(msg);
					//http://api.msg91.com/api/sendhttp.php?route=4&sender=TESTIN&mobiles=9860480753&authkey=261830AZBb5dh2oah5c8f802f&message=Hello! This is a test message&country=91
					//var url="http://api.msg91.com/api/sendhttp.php?country=91&sender=BLMSTR&route=4&mobiles="+this.state.addNumber+"&authkey=261830AgqWxNGm5c600a87&message="+msg;
					var url="http://api.msg91.com/api/sendhttp.php?route=4&sender=BLMSTR&mobiles="+this.phone+"&authkey=261830AZBb5dh2oah5c8f802f&message="+msg;
					console.log(url);
					fetch(url,{
						method:"GET",
						mode:"no-cors"
					})
					.then(console.log("Made fetch req"))
					console.log("Send Sms",results.cust_id,this.state.addNumber);

					fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/updateSMS',{
						method:'post',
						headers:{'Content-Type': 'application/json'},
						body: JSON.stringify({
							cust_id:results[0].cust_id,
							phone:this.phone,
							rest_id:this.props.match.params.rest_id,
							meal_date:corDate,
							sms:Sms
						})
					})

				  })
			}
		})
	}

		/*makeReq()
		{
			console.log("MAke REQ");
			this.getItems();
		}*/


	/*componentDidUpdate(prevProps, prevState)
	{
		if(this.state.details.length !== prevState.details.length)
		{
			this.getItems();
		}
	}*/
	
	componentDidMount(){
		this.getItems();
		this.MyFunction();
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getRestName',{
					method:'post',
					headers:{'Content-Type': 'application/json'},
					body: JSON.stringify({
						rest_id:this.props.match.params.rest_id
					})
				})
				.then(response=>response.json())
				.then(res=>{
					this.setState({sms:res[0].sms})
					console.log(this.state.sms)
				})

	}

	getItems=()=>{
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/home/tables',{
			method:'post',
			headers:{'Content-Type': 'application/json'},
			body:JSON.stringify({
				rest_id:this.props.match.params.rest_id
			})
		})
		.then(response=>response.json())
		.then(results=>{
			this.list=results;
			if(this.state.details.length!==results.length)
			{
				this.setState({'details':results})
			}
			else
			{
				console.log("nonononon");
			}
		});

	}


	onCustSelect=(cust_id)=>{
			console.log("card",cust_id);
			this.props.loadCustomer(cust_id);
			this.props.onRouteChange('quantity');
			this.setState({cust_id:cust_id});
		}



	render()
	{
		const {onRouteChange}=this.props;
		const filteredMenu=this.state.details.filter(tables=>{
		return tables.name.toLowerCase().includes(this.state.search.toLowerCase());
		})

		/*if(!(this.state.details.length>0))
		{
			return(
				<p>Loading...</p>
				)
		}*/

		return(
			<div>
			<Navigation onRouteChange={this.onRouteChange} email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
			<div className="NewCustomer">
					<div className="addTable">
					<p className="addNewBox"><i class="fa fa-plus" aria-hidden="true"></i> Add New Customer</p>
			  		</div>
			  		<hr />
			  		<p className="addName"><i class="fa fa-user" aria-hidden="true"></i> Customer Name</p>
			  		<input onChange={this.onNameChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />
			  		<p className="addNumber"><i class="fa fa-phone" aria-hidden="true"></i> Mobile Number<span className="smsSpan">SMS-{this.state.sms}</span></p>
			  		<input onChange={this.onNumberChange} type="text" className="form-control frmcntrl" placeholder="Mobile Number" aria-label="Mobile Number" aria-describedby="basic-addon2" />
			  		{
			  			this.state.validCheck==='Incorrect Phone Number'?
			  				<div>
			  					<p className="validCheckFalse"><i class="fa fa-ban" aria-hidden="true"></i>{this.state.validCheck}</p>
			  				</div>
			  				:
			  				<div>
			  					<p className="validCheckTrue"><i class="fa fa-check" aria-hidden="true"></i>{this.state.validCheck}</p>
			  				</div>

			  		}
			  		<div className="input-group-append">
			    		<button onClick={this.onSubmitName} className="button shadow-5" type="button">Add Customer</button>
			  		</div>

			  		<div className="searchTables">
			  		<p className="searchBox"><i className="searchIcon fa fa-search" aria-hidden="true"></i> Search Tables</p>
			  		</div>

			  		<div className="input-group searchTable">
		  				<input type="text" className="form-control frmcntrl" onChange = {this.onItemChange} placeholder="Search by Name"/>
					</div>
					
					<div className="refreshButton">
					<button className="buttonRef shadow-5" href="#0" onClick={this.getItems}><i class="fa fa-refresh" aria-hidden="true"></i></button>
					</div>
			</div>
			{
				filteredMenu.map(card=>(
				 <Link to={'/home/tables/'+this.props.match.params.email+'/updateBill/'+this.props.match.params.rest_id+'/'+card.cust_id+'/'+card.name}><div className ='crd grow br3 pa5 ma2 dib bw5 shadow-5' key={card.cust_id} onClick={this.onCustSelect.bind(this,card.cust_id)}>
					<div>
						<p className='id' >{card.cust_id}</p>
						<p  className='name'>{card.name}</p>
					</div>
				</div>
				</Link>
				))}
			</div>
			);
	}

}

export default Cards;


