import React from 'react';
import './Summary.css';
import { Link, Route, Switch } from 'react-router-dom';
import Navigation from '../Navigation/Navigation';
class Summary extends React.Component
{
	constructor({props,rest_id,cust_id}){
		super(props);
		this.gst=0;
		this.type="Shop";
		this.payment_type="Cash";
		this.state={
			'details':[],
			'custDetails':[],
			'analytics':[],
			totAmount:0,
			discount:0,
			total:0,
			gstAmount:0,
			gstin:0,
			gstString:0
		}
	}


	onDiscountChange=(event)=>{
		this.setState({discount : event.target.value});
	}

	calcGST=()=>
	{
		var taxes=(this.state.gst/100)*this.state.totAmount;
		console.log(taxes);
	}

	calcDisc=()=>{
		var disc=(this.state.totAmount)-((this.state.discount/100)*this.state.totAmount)+this.state.gstAmount;
		this.setState({total:disc});
	}

	DateprototypegetWeek=()=>{ 
    var determinedate = new Date(); 
    determinedate.setFullYear(determinedate.getFullYear(), determinedate.getMonth(), determinedate.getDate()); 
    var D = determinedate.getDay(); 
    if(D == 0) D = 7; 
    determinedate.setDate(determinedate.getDate() + (4 - D)); 
    var YN = determinedate.getFullYear(); 
    var ZBDoCY = Math.floor((determinedate.getTime() - new Date(YN, 0, 1, -6)) / 86400000); 
    var WN = 1 + Math.floor(ZBDoCY / 7); 
    return WN; 
    }

	finishMeal=(amt)=>{

		var tempDate = new Date();
  		//var date = tempDate.getFullYear() + '-' + (tempDate.getMonth()+1) + '-' + tempDate.getDate() +' '+ tempDate.getHours()+':'+ tempDate.getMinutes()+':'+ tempDate.getSeconds();
  		var corDate=tempDate.getDate()+'-'+(tempDate.getMonth()+1)+'-'+tempDate.getFullYear();
  		var month=tempDate.getMonth()+1;
  		var day=tempDate.getDay();
  		var week=this.DateprototypegetWeek();

		console.log("In finish Meal");
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/finishMeal',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id,
           name:this.props.match.params.cust_name,
          amount:amt,
		  meal_date:corDate,
		  day:day,
		  month:month,
		  year:tempDate.getFullYear(),
		  week:week,
		  hour:tempDate.getHours(),
		  min:tempDate.getMinutes(),
		  discount:this.state.discount,
		  gst_amount:this.state.gstAmount,
		  order_type:this.type,
		  payment_type:this.payment_type

        })
      })

		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/changeStatus',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id,
          //item:item
        })
      })

	}

	getItems(){

		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/bill',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
          //item:item
        })
      })
		.then(response=>response.json())
		.then(results=>this.setState({'details':results}))

	}

	componentDidMount(){
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getTotal',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
        })
      })
		.then(response=>response.json())
		.then(results=>{
			this.setState({total:results,totAmount:results})
			this.getItems();
		})

		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getRestDetails',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
        })
      })
		.then(response=>response.json())
		.then(results=>{
			if(results[0].gst!=0)
			{
				var gst='Including '+results[0].gst+'% GST';
				var taxes=(results[0].gst/100)*this.state.totAmount;
				var tot=taxes+this.state.totAmount;
				this.setState({gstAmount:taxes})
				this.setState({total:tot})
				this.setState({gstString:gst})
			}
			console.log("GTST",results[0].gst)
			this.gst=results[0].gst;
		})
	}

	
	sendSMS=()=>
	{
		//fetch('http://api.msg91.com/api/sendhttp.php?country=91&sender=BlMstr&route=4&mobiles=9860480753&authkey=261830AX9eTXi4ah5c5c31a3&message=BillMaster test message',{
		//}).then(console.log("Made fetch req"));
		console.log("Send Sms");
		
	}

	setType=(type)=>{
  		this.type=type;
  		this.setState({type:type})
  		console.log(this.type);
  	}

  	setPaymentType=(method)=>
  	{
  		this.payment_type=method;
  		console.log(this.payment_type);
  	}

  	paymentMethod=()=>
  	{
  		let ar=[];
  		{
  			ar.push(<br/>);
			ar.push(<hr/>);
			ar.push(<p className="paymentMethodP">Payment Method</p>);
			ar.push(<form className="radioD">
							<input className="typeI" onClick={this.setPaymentType.bind(this,"Cash")} type="radio" name="type" value="Shop"/><span className="rSpan">Cash</span>
							<input className="typeI" onClick={this.setPaymentType.bind(this,"Card")} type="radio" name="type" value="Swiggy"/><span className="rSpan">Card</span>
							<input className="typeI" onClick={this.setPaymentType.bind(this,"Paytm")} type="radio" name="type" value="Zomato"/><span className="rSpan">Paytm</span>
							<input className="typeI" onClick={this.setPaymentType.bind(this,"Google Pay")} type="radio" name="type" value="UberEats"/><span className="rSpan">Google Pay</span><br/>
							<input className="typeI" onClick={this.setPaymentType.bind(this,"MobiKwik")} type="radio" name="type" value="FoodPanda"/><span className="rSpan">MobiKwik</span>
					</form>);
  		}
  		return ar;
  	}

	render(){		
		return(
			<div>
			<Navigation onRouteChange={this.onRouteChange} email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
			<div className="billSummary">
				<div className="cust_id">
				<p className="custId">Final Bill - {this.props.match.params.cust_id} -<span className="name">     {this.props.match.params.cust_name} </span></p>

				</div>


			  	<table className="tbll">
				    	<tr>
				    	<th className="itm">Item</th>
				    	<th className="prc">Price.</th>
				    	<th className="qnty">Qty.</th>
				    	<th className="amt">Amt.</th>
				    	</tr>
		    {
		    	this.state.details.map(ritem=>(
		    	<tr>
		    	<td className="itm">{ritem.item}</td>
		    	<td className="prc">{ritem.price}</td>
		    	<td className="qnty">{ritem.quantity}</td>
		    	<td className="amt">{(ritem.price*ritem.quantity)}</td>
		    	</tr>
		    ))
			}
			</table>
				<div className="discountT">
			  		<p className="searchBox">% Discount?</p>
			  	</div>
				<div className="searchMenu">
				<input onChange = {this.onDiscountChange} type="text" class="form-control frmcntrl" placeholder="Enter Discount %" aria-describedby="basic-addon2"/>
				</div>
				<div className="input-group-append">
			    	<button className="btn btn-mdb-color buttn shadow-5" type="button" onClick = {this.calcDisc}>Apply %</button>
			  	</div>

			<div className="totalAmount">
				<p className="total">Total-<span className="totAmount">{this.state.total}</span><span><p className="gstP">{this.state.gstString}</p></span></p>
			</div>
			<br/>
			<br/>
			<div className="gstD">
			</div>
			<br/>
			<br/>
			<br/>
			<form className="radioD">
							<input className="typeI" onClick={this.setType.bind(this,"Shop")} type="radio" name="type" value="Shop"/><span className="rSpan">Shop</span>
							<input className="typeI" onClick={this.setType.bind(this,"Swiggy")} type="radio" name="type" value="Swiggy"/><span className="rSpan">Swiggy</span>
							<input className="typeI" onClick={this.setType.bind(this,"Zomato")} type="radio" name="type" value="Zomato"/><span className="rSpan">Zomato</span>
							<input className="typeI" onClick={this.setType.bind(this,"UberEats")} type="radio" name="type" value="UberEats"/><span className="rSpan">UberEats</span><br/>
							<input className="typeI" onClick={this.setType.bind(this,"FoodPanda")} type="radio" name="type" value="FoodPanda"/><span className="rSpan">FoodPanda</span>
						</form>
				
			{
				this.paymentMethod()
			}
			<div className="qr">
		    	<img height="150" width="150" src={`https://api.qrserver.com/v1/create-qr-code/?data=[http://www.billmaster.xyz/customer/bill/${this.props.match.params.rest_id}/${this.props.match.params.cust_id}/${this.props.match.params.cust_name}]&size=[120]x[10]`} />
		    </div>
			
			<div className="buttons">
				<Link to={'/home/tables/'+this.props.match.params.email+'/updateBill/'+this.props.match.params.rest_id+'/'+this.props.match.params.cust_id+'/'+this.props.match.params.cust_name}><button className="button1  shadow-5" href="#0" ><i className="fa fa-arrow-left"></i> Back</button></Link><span>
				<Link to={"/home/tables/"+this.props.match.params.email+"/"+this.props.match.params.rest_id}><button className="button2 shadow-5" onClick={()=>{this.finishMeal(this.state.total)}}><i className="fa fa-check"></i> Submit</button></Link></span>
		    </div>	
		    </div>

		    <br/>
		    <br/>
		    
		    </div>
			)
	}

}


export default Summary;


