import React from 'react';
import './QuantityLoop.css';
//import {List} from './components/Menu/Menu';
import { Link, Route, Switch } from 'react-router-dom';
import Navigation from '../Navigation/Navigation';


class QuantityLoop extends React.Component
{	
	constructor({props, rest_id, cust_id}){
		super(props);
		this.c=0;
		this.type="Shop";
		this.list;
		//this.getitems();
		this.state = {
			'details':[],
			'menu':[],
			cItem:'',
			cItem_no:0,
			qty:1,
			price:0,
			search:'',
			rank:0,
			type:'Shop'
		}
	}


	onItemChange=(event)=>{
		this.setState({search : event.target.value})
		console.log(this.state.search);
	}

	onSubmitAdd=()=>{

		this.getitems();
	}



	getitems=()=>{

		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/bill',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
        })
      })
		.then(response=>response.json())
		.then(results=>{
			if(this.state.details.length !== results.length)
			{
				console.log("YESUPDATE");
				this.setState({'details':results})
			}
			else
			{
				for(var i=0;i<this.state.details.length && this.c!==1;i++)
				{
					if(results[i].quantity!==this.state.details[i].quantity)
					{
						this.c=1;
						console.log("THIS.C is ",this.c);
						this.setState({'details':results})
						console.log("THIS.C is ",this.c);
					}
					else
					{
						console.log("No Qty Update");
					}
				}
			}
		})
		.then(console.log("orders-----",this.state.details))
	}


	componentDidMount(){
		console.log("rest",this.props.match.params.rest_id);
		console.log("cust",this.props.match.params.cust_id);
		this.getitems();
		this.displayMenu();
	}

	componentDidUpdate()
  	{
  		this.displayMenu();
  	}

	
	IncrementItem=(item,clicks,item_no,ser_num)=>{
  	this.list=this.state.details;
  	for(var i=0;i<this.list.length;i++)
  	{
  		if(this.list[i].ser_num===ser_num)
  		{
  			this.list[i].quantity=this.list[i].quantity+1;
  			this.setState({'details':this.list});
  		}
  	}
  	this.setState({'details':this.list});
  	}	

  	DecreaseItem=(item,clicks,item_no,ser_num)=>{
  		this.list=this.state.details;
  	for(var i=0;i<this.list.length;i++)
  	{
  		if(this.list[i].ser_num===ser_num)
  		{
  			this.list[i].quantity=this.list[i].quantity-1;
  			this.setState({'details':this.list});
  		}
  	}
  	this.setState({'details':this.list});
  	}



  	DeleteItem=(item,item_no,ser_num)=>{
  		console.log("DELETE UETNE------",item,item_no);
  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/deleteItem',{
  			method:'POST',
  			headers:{'Content-Type':'application/json'},
  			body:JSON.stringify({
  				item:item,
  				item_no:item_no,
  				cust_id:this.props.match.params.cust_id,
  				rest_id:this.props.match.params.rest_id,
  				ser_num:ser_num
  			})
  		}).then(this.getitems());
  	}


  	displayMenu=()=>{


  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getMenu',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          type:this.type
        })
      })
		.then(response=>response.json())
		.then(results=>this.setState({'menu':results}));
  	
  	}

  	setField=(chosenItem,itemPrice,cItem_no,rank)=>{
  		this.setState({'cItem':chosenItem,'price':itemPrice,'cItem_no':cItem_no,'rank':rank})
  		console.log("Field",this.state.cItem,this.state.price,this.state.qty);
  	}

  	updateBill=()=>{
  		console.log("in udatjw");
  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/updateBill',{
  			method:'post',
  			headers:{'Content-Type':'application/json'},
  			body:JSON.stringify({
  				item:this.state.cItem,
  				quantity:this.state.qty,
  				price:this.state.price,
  				rest_id:this.props.match.params.rest_id,
  				cust_id:this.props.match.params.cust_id,
  				item_no:this.state.cItem_no,
  			})
  		}).then(response=>response.json())
  		.then(results=>{
  			var test=this.state.details.concat(results);
			console.log("REsultsssss------------",test)
			this.setState({'details':test})
  			console.log("Added new item--=------",results);
  		})

  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/updateRank',{
  			method:'post',
  			headers:{'Content-Type':'application/json'},
  			body:JSON.stringify({
  				item:this.state.cItem,
  				rest_id:this.props.match.params.rest_id,
  				item_no:this.state.cItem_no,
  				rank:this.state.rank
  			})
  		});

  		console.log("Updated rank",this.state.menu.rank)

  	}


  	updateQty=(item,clicks,item_no,ser_num)=>
  	{
  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/updateQty',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                item:item,
                clicks:clicks,
                item_no:item_no,
                cust_id:this.props.match.params.cust_id,
                rest_id:this.props.match.params.rest_id,
                ser_num:ser_num
            })
        });
  	}

  	deleteCust=()=>{
  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/deleteCustomer',{
  			method:'post',
  			headers:{'Content-Type':'application/json'},
  			body:JSON.stringify({
  				cust_id:this.props.match.params.cust_id
  			})
  		}).then(this.getitems()).then(this.getitems());
  	}

  	setQty=(qty)=>{
  		this.setState({'qty':qty});
  	}

  	setType=(type)=>{
  		this.type=type;
  		this.setState({type:type})
  		console.log(this.type);
  	}


	render(){
	const {onRouteChange}=this.props;
	const filteredMenu=this.state.menu.filter(items=>{
		return items.item.toLowerCase().includes(this.state.search.toLowerCase());
	})

	return(
		<div>
		<Navigation onRouteChange={this.onRouteChange} email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
			<div className="QtyLoop">
				
				
				<div className="cust_id">
				<p className="custId ">{this.props.match.params.cust_id} - {this.props.match.params.cust_name} </p>
				</div>


				<br/>
				
				<div className="searchFromMenu">
			  		<p className="searchBox"><i className="searchIcon fa fa-search" aria-hidden="true"></i> Search Menu</p>
			  	</div>
			  	<br/>
			  	<br/>
			  	<form className="radioD">
							<input className="typeI" onClick={this.setType.bind(this,"Shop")} type="radio" name="type" value="Shop" /><span className="rSpan">Shop</span>
							<input className="typeI" onClick={this.setType.bind(this,"Swiggy")} type="radio" name="type" value="Swiggy"/><span className="rSpan">Swiggy</span>
							<input className="typeI" onClick={this.setType.bind(this,"Zomato")} type="radio" name="type" value="Zomato"/><span className="rSpan">Zomato</span>
							<input className="typeI" onClick={this.setType.bind(this,"UberEats")} type="radio" name="type" value="UberEats"/><span className="rSpan">UberEats</span><br/>
							<input className="typeI" onClick={this.setType.bind(this,"FoodPanda")} type="radio" name="type" value="FoodPanda"/><span className="rSpan">FoodPanda</span>
						</form>
				<div className="searchMenu">
				<input onChange = {this.onItemChange} type="text" class="form-control frmcntrl" placeholder="Search Menu" aria-describedby="basic-addon2"/>
				</div>

				<div className="box">
					<div className="ScrollBox" style={{ overflow: 'scroll', border: '1px solid orange', height: '200px'}}>

						<div className="DisplayMenu"  >
						{
							filteredMenu.map(items=>(
							<p className="itemList" key={items.item_no} onClick={this.setField.bind(this,items.item,items.price,items.item_no,items.rank)}>{items.item}-{items.price}</p>
							)
							)
						}
						</div>
					</div>
					<p className="choiceDisplay">Selected- <span className="dispSelected">{this.state.qty}-{this.state.cItem}</span></p>
				</div>



				<div class="itemQty">
					<div className="qtybuttons">
				  		<button className="button5 shadow-5" onClick={this.setQty.bind(this,1)}>1</button>
				  		<button className="button5 shadow-5" onClick={this.setQty.bind(this,2)}>2</button>
				  		<button className="button5 shadow-5" onClick={this.setQty.bind(this,3)}>3</button>
				  		<button className="button5 shadow-5" onClick={this.setQty.bind(this,4)}>4</button>
				  		<button className="button5 shadow-5" onClick={this.setQty.bind(this,5)}>5</button>
				  		<button className="button5 shadow-5" onClick={this.setQty.bind(this,6)}>6</button>
				  		<button className="button5 shadow-5" onClick={this.setQty.bind(this,7)}>7</button>
				  	</div>
			  		<br />
			  		<div className="input-group-append">
			    	<button className="btn btn-mdb-color buttn shadow-5" type="button" onClick = {this.updateBill}><i class="fa fa-check-square-o"></i> Add</button>
			  	</div>

			  	<div className="refreshButton">
					<button className="buttonRef shadow-5" href="#0" onClick={this.getitems}><i class="fa fa-refresh" aria-hidden="true"></i></button>
					</div>
			  	</div>
					  	<table>
						    	<tr>
						    	<th className="Qty">Qty.</th>
						    	<th className="item">Item</th>
						    	<th className="amt">Amt.</th>
						    	</tr>
							  	{
							  		this.state.details.map(ritem => (

							  		<tr key={ritem.item_no}>
							  		
							  		<td className="qtyBtn">
							    	<div class="input-group plus-minus-input">
									<div class="input-group-button">
								    	<button type="button" className="button hollow circle  shadow-5" data-quantity="plus" onClick={this.IncrementItem.bind(this,ritem.item,ritem.quantity,ritem.item_no,ritem.ser_num)} data-field="quantity">
								      	<i class="fa fa-plus" aria-hidden="true"></i>
								    	</button>
								  	</div>
								  	
								  	<p class="input-group-field" type="number" name="quantity">{this.state.clicks=ritem.quantity}</p>
								  	
								  	<div class="input-group-button">
								    	<button type="button" class="button hollow circle shadow-5" data-quantity="minus" onClick={this.DecreaseItem.bind(this,ritem.item,ritem.quantity,ritem.item_no,ritem.ser_num)}  data-field="quantity">
								      	<i class="fa fa-minus" aria-hidden="true"></i>
								    	</button>
								    	<div className="updateQty">
								    	<button className="buttonRef  shadow-5" href="#0" onClick={this.updateQty.bind(this,ritem.item,ritem.quantity,ritem.item_no,ritem.ser_num)}><i class="fa fa-refresh" aria-hidden="true"></i></button>
								  		</div>
								  	</div>
									</div>
									</td>

							    	<td className="item" key={ritem.item_no}>{this.state.item=ritem.item}</td>
							    	<td className="amt">{ritem.price}</td>
							    	<td className="del1"><button className="delbtn button shadow-5" href="#0" onClick={this.DeleteItem.bind(this,ritem.item,ritem.item_no,ritem.ser_num)} ><i class="fa fa-close"></i></button></td>
							    	</tr>))
							  	}
				  		</table>
			  	<div className="buttons">
				  	<Link to={"/home/tables/"+this.props.match.params.email+"/"+this.props.match.params.rest_id}><button className="button1  shadow-5" href="#0" onClick={()=>onRouteChange('tables')}><i class="fa fa-arrow-left"></i> Back</button></Link><span>
				  	<Link to={'/home/tables/'+this.props.match.params.email+'/updateBill/'+this.props.match.params.rest_id+'/'+this.props.match.params.cust_id+'/'+this.props.match.params.cust_name+'/summary'}><button className="button2  shadow-5" href="#0" onClick={()=>onRouteChange('summary')}><i class="fa fa-check"></i> Submit</button></Link></span>
				</div>
				<div className="empty">
				<p className="blankLine">_</p>
				</div>
			</div>
				<div className="delCust">
			  		<Link to={"/home/tables/"+this.props.match.params.email+"/"+this.props.match.params.rest_id}><button className="button2  shadow-5" onClick={this.deleteCust}><i class="fa fa-close"></i> Delete</button></Link>
				</div>
				<br />
				<br />

			</div>
		)	
}
	
}

export default QuantityLoop;


