import React from 'react';
import {Link,Route,Switch} from 'react-router-dom';
import Navigation from '../Navigation/Navigation';
import './Menu.css';
class Menu extends React.Component
{
	constructor({props})
	{
		super(props);
		this.list;
		this.type="Shop";
		this.state={
			'menu':[],
			search:'',
			item:'',
			item_no:0,
			price:0,
			type:'Shop'
		}
	}

	onSearchChange=(event)=>{
		this.setState({search : event.target.value})
		console.log(this.state.search);
	}

	onPriceChange=(event)=>{
		this.setState({price : event.target.value})
		console.log(this.state.price);
	}

	onItemChange=(event)=>{
		this.setState({item : event.target.value})
		console.log(this.state.item);
	}

	displayMenu=()=>{
  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getMenu',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          type:this.state.type
        })
      })
		.then(response=>response.json())
		.then(results=>{
			this.list=results;
			console.log("In displayMenu",results.length,this.state.menu.length,results);
				if(results.length !== this.state.menu.length)
				{
					this.setState({'menu':results})
				}
				else
				{
					console.log("No need to update");
				}
		});
  	}


  	addItem=()=>
  	{
  		console.log("Inserting new item---",this.props.match.params.rest_id,this.state.item,this.state.price);
  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/addItem',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          item:this.state.item,
          price:this.state.price,
          type:this.type
        })
      }).then(response=>response.json())
  		.then(results=>{
  			this.setState({'menu':results})
  		})
  	}

  	delItem=(item_no)=>
  	{
  		console.log("Deleting---",item_no);
  		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/deleteItemFromMenu',{
  			method:'post',
  			headers:{'Content-Type':'application/json'},
  			body:JSON.stringify({
  				item_no:item_no
  			})
  		}).then(this.displayMenu()).then(this.displayMenu());
  	}

  	componentDidUpdate()
  	{
  		this.displayMenu();
  	}

  	componentDidMount()
  	{
  		this.displayMenu();
  	}

  	setType=(type)=>{
  		this.type=type;
  		this.setState({type:type})
  		console.log(this.type);
  	}


  	render()
  	{
  		const filteredMenu=this.state.menu.filter(items=>{
		return items.item.toLowerCase().includes(this.state.search.toLowerCase());
		})

		

  		return(
  			<div>
  			<Navigation onRouteChange={this.onRouteChange} email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
	  				<div className="DisplayMenu">
					<div className="AddNew">
						<p className="addNewBox"><i class="fa fa-plus" aria-hidden="true"></i> Add New Item</p>
						<div className="additembox"><input onChange = {this.onItemChange} type="text" className="form-control frmcntrl" placeholder="Item Name" aria-describedby="basic-addon2"/></div>
						<div className="addpricebox"><input onChange = {this.onPriceChange} type="text" className="form-control frmcntrl" placeholder="Price" aria-describedby="basic-addon2"/></div>
						<form className="radioD">
							<input className="typeI" onClick={this.setType.bind(this,"Shop")} type="radio" name="type" value="Shop" /><span className="rSpan">Shop</span>
							<input className="typeI" onClick={this.setType.bind(this,"Swiggy")} type="radio" name="type" value="Swiggy"/><span className="rSpan">Swiggy</span>
							<input className="typeI" onClick={this.setType.bind(this,"Zomato")} type="radio" name="type" value="Zomato"/><span className="rSpan">Zomato</span>
							<input className="typeI" onClick={this.setType.bind(this,"UberEats")} type="radio" name="type" value="UberEats"/><span className="rSpan">UberEats</span><br/>
							<input className="typeI" onClick={this.setType.bind(this,"FoodPanda")} type="radio" name="type" value="FoodPanda"/><span className="rSpan">FoodPanda</span>
						</form>
						<div className="addNewItemBtn"><button className="button3" href="#0" onClick={this.addItem}><i class="fa fa-plus"></i> Add</button></div>
						<div className="refreshbutton"><button className="buttonRef" onClick={this.displayMenu}><i class="fa fa-refresh" aria-hidden="true"></i></button></div>
					</div>
					<div className="searchMenu">
						<p className="searchBox"><i className="searchIcon fa fa-search" aria-hidden="true"></i> Search Menu</p>
						<div className="searchitembox"><input onChange = {this.onSearchChange} type="text" className="form-control frmcntrl" placeholder="Search Menu" aria-describedby="basic-addon2"/></div>
					</div>
					
					<div><p className="menu">Menu.</p></div>
					<form className="radioDFilter">
							<input className="typeI" onClick={this.setType.bind(this,"Shop")} type="radio" name="type" value="Shop" /><span className="rSpan">Shop</span>
							<input className="typeI" onClick={this.setType.bind(this,"Swiggy")} type="radio" name="type" value="Swiggy"/><span className="rSpan">Swiggy</span>
							<input className="typeI" onClick={this.setType.bind(this,"Zomato")} type="radio" name="type" value="Zomato"/><span className="rSpan">Zomato</span>
							<input className="typeI" onClick={this.setType.bind(this,"UberEats")} type="radio" name="type" value="UberEats"/><span className="rSpan">UberEats</span><br/>
							<input className="typeI" onClick={this.setType.bind(this,"FoodPanda")} type="radio" name="type" value="FoodPanda"/><span className="rSpan">FoodPanda</span>
						</form>
					<table className="menuTable">
						<tr>
						   	<th className="item">Item</th>
						   	<th className="price">Price</th>
					   	</tr>
					</table>
						{
							filteredMenu.map(items=>(
								<table>
							    	<tr>
								    <td className="item" key={items.item_no}>{items.item}</td>
								    <td className="amt">{items.price}</td>
								    <td className="del1"><button className="delbtn button" onClick={this.delItem.bind(this,items.item_no)}><i class="fa fa-close"></i></button></td>
								    <td className="nextbnrow"><Link to={"/home/restaurant/menu/update/"+this.props.match.params.email+"/"+this.props.match.params.rest_id+"/"+items.item+"/"+items.item_no+"/"+items.price}><button className="nextbtn button"><i class="fa fa-pencil"></i></button></Link></td>
								    </tr>
					  			</table>
							)
							)
						}
					</div>
				</div>

  			)
  	}


}

export default Menu;




