import React from 'react';
import './Button.css';
import './Login.css';
import { Link, Route, Switch } from 'react-router-dom';
import Home from '../Home/Home';
import NavigationLogin from '../Navigation/NavigationLogin';


class Login extends React.Component{

	
	constructor({props})
	{
		super(props);
		this.data;
		this.state={
			'details':[],
			loginEmail:'',
			loginPassword:'',
			result:'loggedOut',
			rest_id:0,
			lemail:'',
			lrest:0,
			average:0,
			res:0,
			login:0
		}
	}



	onEmailChange=(event)=>{
		this.setState({loginEmail: event.target.value})
	}

	onPasswordChange=(event)=>{
		this.setState({loginPassword:event.target.value})
	}


  	updateDetails(resp){
  		this.setState((state)=>{

  			console.log("in updateDetails",resp);
  			return {'details':resp}
  		})
  	}

  	updateEmail(resp){
  		this.setState((state)=>{

  			console.log("In update Email",resp);
  			return{lemail:resp}

  		})
  	}

  	updateRest(resp){
  		this.setState((state)=>{

  			console.log("In update Rest",resp);
  			return{lrest:resp}
  		})
  	}


  	acknowledge=()=>{
  		//if(resp==="yes")
  		{
  			console.log("ACknowldegeddsfs------");
  		this.setState((state)=>{
  			console.log("Acknowledgeing login credentials ");
  			return{res:1}
  		})
  	}
  	}


	onSubmitLogin=()=>
	{
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/login',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
				email:this.state.loginEmail,
				hash:this.state.loginPassword
			})
		})
		.then(response=>response.json())
		.then(response=>{
			if(response[0].email===this.state.loginEmail){
				this.setState({'details':response[0]})
				//setTimeout(() => this.afterLogin(),2000);
				console.log("yupppPPPP",response);
				console.log(response[0].end_date);
				var days=this.calcDate(response[0].end_date);
				if(days>0)
				{
					this.props.onRouteChange('home');
					this.setState({login:1})
				}
				else
				{
					this.props.onRouteChange('signIn');
					this.setState({login:1})
				}
				this.props.loadDaysLeft(days);
				console.log("mlskmsdklnfl---------",this.state.details.email);
				this.props.loadUser(response[0],days);
				
				if(days<=0)
				{
					this.stopService();
				}
			}
		})
		.then(console.log("yess----------",this.state.lemail))
	}

	stopService=()=>
	{
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/disableaccount',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
				email:this.state.loginEmail,
				rest_id:this.state.details.rest_id
			})
		})
	}

	calcDate=(date)=>
	{
		var tempDate = new Date();
  		//var date = tempDate.getFullYear() + '-' + (tempDate.getMonth()+1) + '-' + tempDate.getDate() +' '+ tempDate.getHours()+':'+ tempDate.getMinutes()+':'+ tempDate.getSeconds();
  		var corDate=tempDate.getDate()+'-'+(tempDate.getMonth()+1)+'-'+tempDate.getFullYear();
		var today = corDate;
		today = new Date(today.split('-')[2],today.split('-')[1]-1,today.split('-')[0]);
		var date2 = date;
		date2 = new Date(date2.split('-')[2],date2.split('-')[1]-1,date2.split('-')[0]);
		var timeDiff = (date2.getTime() - today.getTime());
		var diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24));
		console.log("mlskmsdklnfl---------",diffDays);
		return diffDays;
	}

	routing=()=>
	{
		console.log('LOGNONOGG===',this.state.login);
		let child=[];
		if(this.state.login==1)
		{
			child.push(<Link to={"/home/"+this.state.result+"/"+this.state.details.email}><button  className="button" onClick={this.onSubmitLogin} href="#0">Login</button></Link>);
		}
		else if(this.state.login==0)
		{
			child.push(<Link to={"/"}><button  className="button" onClick={this.onSubmitLogin} href="#0">Login</button></Link>)
		}
		return child;
	}


	componentDidMount(){
		/*this.acknowledge();
		fetch('https://billmaster-backend.appspot.com/',{
		}).then(console.log("Made fetch req"))*/
	}

	render(){
		const {onRouteChange}=this.props;
		const{result}=this.state;
		console.log("credentials-------",this.state.res);
		return(
			<div>
			<NavigationLogin/>
				<main className="pa4 black-80">
				  <form className="measure center">
				    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
				      <div className="mt3">
				        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
				        <input onChange={this.onEmailChange} className="input1 pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address"/>
				      </div>
				      <div className="mv3">
				        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
				        <input onChange={this.onPasswordChange} className="input2 b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password"/>
				      </div>
						</fieldset>
				      <div className="">
				      	{this.routing()}
				      	<p className="tcP">By logging in you agree to our <span className="tcS"><Link to="/termsAndConditions">Terms and Conditons</Link></span></p>
				      </div>
				  </form>

				</main>
				</div>
			)
		}
}



export default Login;


