import React from 'react';
import './qtybtn.css';

class qtybtn extends React.Component{

	constructor(props)
	{
		super(props);
		this.state={
			clicks:0
		}

	}

	IncrementItem = () => {
    this.setState({ clicks: this.state.clicks + 1 });
    console.log(this.state.clicks);
    this.updateQty(this.state.clicks);
  	}	

  	DecreaseItem = () => {
    this.setState({ clicks: this.state.clicks - 1 });
    console.log(this.state.clicks);
    this.updateQty(this.state.clicks);
  	}
  

  	updateQty(clicks)
  	{
  		fetch('http://localhost:3000/updateQty',{
  			method:'post',
  			headers:{'Content-Type':'application/json'},
  			body:JSON.stringify({
  				clicks:clicks,
  			})
  		})
  	}




	render(){
		return(
			<div class="input-group plus-minus-input">
			  	<div class="input-group-button">
			    	<button type="button" class="button hollow circle" data-quantity="minus" onClick={this.DecreaseItem}  data-field="quantity">
			      	<i class="fa fa-minus" aria-hidden="true"></i>
			    	</button>
			  	</div>
			  	<p class="input-group-field" type="number" name="quantity">{this.state.clicks}</p>
			  	<div class="input-group-button">
			    	<button type="button" class="button hollow circle" data-quantity="plus" onClick={this.IncrementItem} data-field="quantity">
			      	<i class="fa fa-plus" aria-hidden="true"></i>
			    	</button>
			  	</div>
			</div>
			);
		}
}


export default qtybtn;


