import React from 'react';
import './Summary.css';
import { Link, Route, Switch } from 'react-router-dom';
import NavigationLogin from '../Navigation/NavigationLogin';
class Summary extends React.Component
{
    constructor({props,rest_id,cust_id}){
        super(props);
        this.rating=0;
        this.ratingService=0;
        this.ratingAmbience=0;
        this.state={
            'details':[],
            'custDetails':[],
            'analytics':[],
            totAmount:0,
            discount:'',
            total:0,
            'ebill':[],
            newTotAmount:0,
            rating:0,
            ratingService:0,
            ratingAmbience:0,
            date:'',
            gstAmount:0,
            gstin:0,
            gstString:0
        }
    }


    onDiscountChange=(event)=>{
        this.setState({discount : event.target.value});
    }

    calcDisc=()=>{
        var disc=(this.state.totAmount)-((this.state.discount/100)*this.state.totAmount);
        this.setState({total:disc});
    }

    DateprototypegetWeek=()=>{ 
    var determinedate = new Date(); 
    determinedate.setFullYear(determinedate.getFullYear(), determinedate.getMonth(), determinedate.getDate()); 
    var D = determinedate.getDay(); 
    if(D == 0) D = 7; 
    determinedate.setDate(determinedate.getDate() + (4 - D)); 
    var YN = determinedate.getFullYear(); 
    var ZBDoCY = Math.floor((determinedate.getTime() - new Date(YN, 0, 1, -6)) / 86400000); 
    var WN = 1 + Math.floor(ZBDoCY / 7); 
    return WN; 
    }


    getItems(){

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/bill',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
          //item:item
        })
      })
        .then(response=>response.json())
        .then(results=>this.setState({'details':results}))

    }

    componentDidMount(){
        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getTotal',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
        })
      })
        .then(response=>response.json())
        .then(results=>{
            this.setState({total:results,totAmount:results})
            this.getItems();
        })

          fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getRestName',{
            method:'post',
            headers:{'Content-Type': 'application/json'},
            body: JSON.stringify({
                cust_id:this.props.match.params.cust_id,
                rest_id:this.props.match.params.rest_id
            })
        })
        .then(response=>response.json())
        .then(data=>{
            console.log("EBILL_______",data)
            this.setState({ebill:data[0]})
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getDiscount',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
        })
      })
        .then(response=>response.json())
        .then(results=>{
            console.log(results);
            var discount=results[0].discount;
            this.setState({date:results[0].meal_date})
            if(discount==0)
            {
                this.setState({discount:''})
            }
            else
            {
                var discAmt=(this.state.total)-(this.state.total*discount/100);
                var temp="With "+discount+"% discount";
                console.log(discAmt);
                this.setState({'discount':temp,total:discAmt})
            }
    })
    
        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/getRestDetails',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
        })
      })
        .then(response=>response.json())
        .then(results=>{
            if(results[0].gst!=0)
            {
                var gst='Including '+results[0].gst+'% GST';
                var taxes=(results[0].gst/100)*this.state.totAmount;
                var tot=taxes+this.state.total;
                this.setState({gstAmount:taxes})
                this.setState({total:tot})
                this.setState({gstString:gst})
            }
            console.log("GTST",results[0].gst)
            this.gst=results[0].gst;
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/rating',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
        })
      })
        .then(response=>response.json())
        .then(results=>{
            if(results[0].food_review!=0)
            {
                this.setState({rating:results[0].food_review})
                this.rating=results[0].food_review;
                this.setStar2();
            }
            if(results[0].service_review!=0)
            {
                this.setState({ratingService:results[0].service_review})
                this.ratingService=results[0].service_review;
                this.setStar3();
            }

            if(results[0].ambience_review!=0)
            {
                this.setState({ratingAmbience:results[0].ambience_review})
                this.ratingAmbience=results[0].ambience_review;
                this.setStar3();
            }

        })

    }

    updateRating=(star)=>
    {
        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/setRatingFood',{
            method:'post',
            headers:{'Content-Type': 'application/json'},
            body: JSON.stringify({
                cust_id:this.props.match.params.cust_id,
                rest_id:this.props.match.params.rest_id,
                food_review:star
            })
        })
    }



    updateRatingService=(star)=>
    {
        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/setRatingService',{
            method:'post',
            headers:{'Content-Type': 'application/json'},
            body: JSON.stringify({
                cust_id:this.props.match.params.cust_id,
                rest_id:this.props.match.params.rest_id,
                service_review:star
            })
        })
    }

    updateRatingAmbience=(star)=>
    {
        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/setRatingAmbience',{
            method:'post',
            headers:{'Content-Type': 'application/json'},
            body: JSON.stringify({
                cust_id:this.props.match.params.cust_id,
                rest_id:this.props.match.params.rest_id,
                ambience_review:star
            })
        })
    }

    setStar=(star)=>
    {   
        this.setState({rating:star})
        this.rating=star;
        this.updateRating(star);
        console.log(this.state.rating);
    }

    setStarService=(star)=>
    {   
        this.setState({ratingService:star})
        this.ratingService=star;
        this.updateRatingService(star);
        console.log(this.state.ratingService);
    }

    setStarAmbience=(star)=>
    {   
        this.setState({ratingAmbience:star})
        this.ratingAmbience=star;
        this.updateRatingAmbience(star);
        console.log(this.state.ratingAmbience);
    }

    setStar2=()=>
    {
        let child=[];
        for(let i=0;i<this.rating;i++)
        {
            child.push(<i className="fa fa-star" aria-hidden="true"></i>)
                console.log(i);
        }
        for(let j=this.rating;j<5;j++)
        {
            child.push(<i className="fa fa-star-o" aria-hidden="true"></i>)
        }
        return child;
    }

    setStar3=()=>
    {
        let child=[];
        for(let i=0;i<this.ratingService;i++)
        {
            child.push(<i className="fa fa-star" aria-hidden="true"></i>)
                console.log(i);
        }
        for(let j=this.ratingService;j<5;j++)
        {
            child.push(<i className="fa fa-star-o" aria-hidden="true"></i>)
        }
        return child;
    }


    setStar4=()=>
    {
        let child=[];
        for(let i=0;i<this.ratingAmbience;i++)
        {
            child.push(<i className="fa fa-star" aria-hidden="true"></i>)
                console.log(i);
        }
        for(let j=this.ratingAmbience;j<5;j++)
        {
            child.push(<i className="fa fa-star-o" aria-hidden="true"></i>)
        }
        return child;
    }


    render(){       
        return(
            <div>
                <NavigationLogin/>
                 <div className="billSummary">
                 <p className="ebillT">E-Bill</p>
                 <div className="rNameD">
                    <p className ="rName">{this.state.ebill.rest_name} </p>  
                </div>
                    <p className="rAddressP">{this.state.ebill.address}</p>
                    <p><span className="date">{this.state.meal_date}</span><span className ="time">{/*18:30*/}</span></p>
                    <br />
                    <table className="tbll">
                        <tr>
                            <th >Item</th>
                            <th >Price.</th>
                            <th >Qty.</th>
                            <th >Amt.</th>
                        </tr>
                    {
                        this.state.details.map(ritem=>(
                        <tr>
                            <td >{ritem.item}</td>
                            <td >{ritem.price}</td>
                            <td >{ritem.quantity}</td>
                            <td >{(ritem.price*ritem.quantity)}</td>
                        </tr>
                        ))
                    }
                    </table>
                    <br/>
                    <div className="totalD">
                        <p className="totalP">Total-<span className="totAmount">{this.state.total}</span><span><p className="gstP">{this.state.gstString}</p><p className="gstP">{this.state.discount}</p></span></p>
                    </div>
                    <br/>
                    <br/>
                    <br/>
                    <br/>
                    <hr/>
                    <br/>
                    <p className="ratingP">Rate the food-</p>
                    {
                        this.state.rating===0?
                        <div className="ratingD">
                            <h1>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStar.bind(this,1)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStar.bind(this,2)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStar.bind(this,3)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStar.bind(this,4)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStar.bind(this,5)}></i>
                            </h1>
                        </div>
                        :
                        <h1 className="yratingD">
                            {this.setStar2()}
                        </h1>
                    }
                    <p className="ratingP">Rate the service-</p>
                    {
                        this.state.ratingService===0?
                        <div className="ratingD">
                            <h1>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarService.bind(this,1)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarService.bind(this,2)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarService.bind(this,3)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarService.bind(this,4)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarService.bind(this,5)}></i>
                            </h1>
                        </div>
                        :
                        <h1 className="yratingD">
                            {this.setStar3()}
                        </h1>
                    }

                    <p className="ratingP">Rate the ambience-</p>
                    {
                        this.state.ratingAmbience===0?
                        <div className="ratingD">
                            <h1>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarAmbience.bind(this,1)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarAmbience.bind(this,2)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarAmbience.bind(this,3)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarAmbience.bind(this,4)}></i>
                            <i className="fa fa-star-o" aria-hidden="true" onClick={this.setStarAmbience.bind(this,5)}></i>
                            </h1>
                        </div>
                        :
                        <h1 className="yratingD">
                            {this.setStar4()}
                        </h1>
                    }
                </div>
            </div>
            )
    }

}


export default Summary;


