import React from 'react';
import { Link, Route, Switch } from 'react-router-dom';
import Home from '../Home/Home';
import './Register.css';
import NavigationLogin from '../Navigation/NavigationLogin';

class Register extends React.Component{
	constructor({props})
	{
		super({props});
		this.sms=0;
		this.date=0;
		this.GST=0;
		this.GSTIN=0;
		this.type=0;
		this.state={
			email:'',
			password:'',
			password2:'',
			rest_name:'',
			address:'',
			phone:'',
			validP:'',
			validPhone:'',
			name:''
		}
	}

	onEmailChange=(event)=>{
		this.setState({email:event.target.value})
	}

	onPasswordChange=(event)=>{
		this.setState({password:event.target.value})
	} 

	onPasswordTwoChange=(event)=>{
		var password2;
		password2=event.target.value;
		console.log(password2);
		if(password2===this.state.password)
		{
			this.setState({validP:'Passwords match'})
		}
		else
		{
			this.setState({validP:'Passwords don\'t match'})
		}
	}

	onRestNameChange=(event)=>{
		this.setState({rest_name:event.target.value})
	}

	onRestAddressChange=(event)=>{
		this.setState({address:event.target.value})
	}

	onPhoneChange=(event)=>{
		var phone=event.target.value;
		if(phone.length===10)
		{
			this.setState({validPhone:'Phone number valid'})
			this.setState({phone:phone})
		}
		else
		{
			this.setState({validPhone:'Invalid phone number'})
		}
	}

	onNameChange=(event)=>{
		this.setState({name:event.target.value})
	}

	onGSTChange=(event)=>{
		this.GST=event.target.value;
	}

	onGSTINChange=(event)=>{
		this.GSTIN=event.target.value;
	}

	onTypeChange=(event)=>{
		this.type=event.target.value;
	}

	onSubmitName=()=>{
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/newlogin',{
			method:'post',
			headers:{'Content-Type': 'application/json'},
			body: JSON.stringify({
				email:this.state.email,
				password:this.state.password
			})
		})
	}

	onSubmitDetails=()=>{
		var msg="Thank you for using BillMaster. Your email Id- "+this.state.email+" Password- "+this.state.password;
					console.log(msg);
					var url="http://api.msg91.com/api/sendhttp.php?country=91&sender=BLMSTR&route=4&mobiles="+this.state.phone+"&authkey=261830AZBb5dh2oah5c8f802f&message="+msg;
					console.log(url);
					fetch(url,{
						method:"GET",
						mode:"no-cors"
					}).then(console.log("Made fetch req"));

		var tempDate = new Date();
  		//var date = tempDate.getFullYear() + '-' + (tempDate.getMonth()+1) + '-' + tempDate.getDate() +' '+ tempDate.getHours()+':'+ tempDate.getMinutes()+':'+ tempDate.getSeconds();
  		var time=tempDate.getHours()+':'+ tempDate.getMinutes();
  		var corDate=(tempDate.getDate())+'-'+(tempDate.getMonth()+1)+'-'+tempDate.getFullYear();
  		console.log(corDate);
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/restdetails',{
			method:'post',
			headers:{'Content-Type': 'application/json'},
			body: JSON.stringify({
				email:this.state.email,
				rest_name:this.state.rest_name,
				address:this.state.address,
				phone:this.state.phone,
				sms:this.sms,
				end_date:this.date,
				gst:this.GST,
				gstin:this.GSTIN,
				type:this.type
			})
		})
	}

	onSMSChange=(event)=>{
		this.sms=event.target.value;
		console.log(this.sms);
	}

	onDateChange=(event)=>{
		this.date=event.target.value;
		console.log(this.date);
	}

	render()
	{
		return(
			<div>
				<NavigationLogin/>
				<div className="parent">
					<p className="pageLabel">Register.</p>
					<br/>
					<br/>
					<hr/>
					<p className="inputLabel">Email</p>
					<input onChange={this.onEmailChange} type="email" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />

					<p className="inputLabel">Password</p>
					<input onChange={this.onPasswordChange} type="password" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />
					
					<p className="inputLabel">Confirm Password</p>
					<input onChange={this.onPasswordTwoChange} type="password" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />

					{
						this.state.validP==='Passwords match'?
							<div>
								<p className="valid"><i class="fa fa-check" aria-hidden="true"></i>{this.state.validP}</p>
							</div>
							:
							<div>
								<p className="notValid"><i class="fa fa-ban" aria-hidden="true"></i>{this.state.validP}</p>						
							</div>
					}	

					<button onClick={this.onSubmitName} className="button shadow-5" type="button">Submit</button>
					<br/>					

					<p className="inputLabel">Restaurant Name</p>
					<input onChange={this.onRestNameChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />
					
					<p className="inputLabel">Restaurant Address</p>
					<input onChange={this.onRestAddressChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />

					<p className="inputLabel">Name</p>
					<input onChange={this.onNameChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />
					
					<p className="inputLabel">Phone</p>
					<input onChange={this.onPhoneChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />
					{
						this.state.validPhone==='Phone number valid'?
							<div>
								<p className="valid"><i class="fa fa-check" aria-hidden="true"></i>{this.state.validPhone}</p>
							</div>
							:
							<div>
								<p className="notValid"><i class="fa fa-ban" aria-hidden="true"></i>{this.state.validPhone}</p>						
							</div>
					}

					<p className="inputLabel">SMS</p>
					<input onChange={this.onSMSChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />

					<p className="inputLabel">Date</p>
					<input onChange={this.onDateChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />

					<p className="inputLabel">GST</p>
					<input onChange={this.onGSTChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />

					<p className="inputLabel">GSTIN</p>
					<input onChange={this.onGSTINChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />

					<p className="inputLabel">Type</p>
					<input onChange={this.onTypeChange} type="text" className="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />

					<button onClick={this.onSubmitDetails} className="button shadow-5" type="button">Submit</button>
				</div>
			</div>
			)
	}
}

export default Register;


