/*import React from 'react';
import './QuantityLoop.css';
//import {List} from './components/Menu/Menu';
import { Link, Route, Switch } from 'react-router-dom';
import Navigation from '../Navigation/Navigation';


class QuantityLoop extends React.Component
{   
    constructor({props, rest_id, cust_id}){
        super(props);
        this.c=0;
        //this.getitems();
        this.state = {
            'details':[],
            'menu':[],
            cItem:'',
            cItem_no:0,
            qty:0,
            price:0,
            search:'',
            rank:0
        }
    }


    onItemChange=(event)=>{
        this.setState({search : event.target.value})
        console.log(this.state.search);
    }

    onQuantityChange=(event)=>{
        this.setState({qty : event.target.value})
        console.log(this.state.qty);
    }

    onSubmitAdd=()=>{

        this.getitems();
    }



    getitems=()=>{

        fetch('https://billmaster-backend.appspot.com/bill',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id,
          cust_id:this.props.match.params.cust_id
        })
      })
        .then(response=>response.json())
        .then(results=>{
            if(this.state.details.length !== results.length)
            {
                console.log("YESUPDATE");
                this.setState({'details':results})
            }
            else
            {
                for(var i=0;i<this.state.details.length && this.c!==1;i++)
                {
                    if(results[i].quantity!==this.state.details[i].quantity)
                    {
                        this.c=1;
                        console.log("THIS.C is ",this.c);
                        this.setState({'details':results})
                        console.log("THIS.C is ",this.c);
                    }
                    else
                    {
                        console.log("No Qty Update");
                    }
                }
            }
        })
        .then(console.log("orders-----",this.state.details))
    }


    componentDidMount(){
        console.log("rest",this.props.match.params.rest_id);
        console.log("cust",this.props.match.params.cust_id);
        this.getitems();
        this.displayMenu();
    }

    componentDidUpdate(prevProps,prevState)
    {
        if((this.state.details.length!==prevState.details.length)||(this.c===1))
        {
            this.getitems();
            this.c=0;
            console.log("Now we're here---",this.c);
        }
        else
        {
            console.log("No update");
        }

    }
    
    IncrementItem=(item,clicks,item_no,ser_num)=>{
    console.log("Increment",item);
    fetch('https://billmaster-backend.appspot.com/updateQtyInc',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                item:item,
                clicks:clicks,
                item_no:item_no,
                cust_id:this.props.match.params.cust_id,
                rest_id:this.props.match.params.rest_id,
                ser_num:ser_num
            })
        }).then(this.getitems()).then(this.getitems());
    }   

    DecreaseItem=(item,clicks,item_no,ser_num)=>{
    fetch('https://billmaster-backend.appspot.com/updateQtyDec',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                item:item,
                clicks:clicks,
                item_no:item_no,
                cust_id:this.props.match.params.cust_id,
                rest_id:this.props.match.params.rest_id,
                ser_num:ser_num
            })
        }).then(this.getitems()).then(this.getitems());
    }



    DeleteItem=(item,item_no,ser_num)=>{
        console.log("DELETE UETNE------",item,item_no);
        fetch('https://billmaster-backend.appspot.com/deleteItem',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                item:item,
                item_no:item_no,
                cust_id:this.props.match.params.cust_id,
                rest_id:this.props.match.params.rest_id,
                ser_num:ser_num
            })
        }).then(this.getitems()).then(this.getitems());
    }


    displayMenu=()=>{


        fetch('https://billmaster-backend.appspot.com/getMenu',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          rest_id:this.props.match.params.rest_id
        })
      })
        .then(response=>response.json())
        .then(results=>this.setState({'menu':results}));
    
    }

    setField=(chosenItem,itemPrice,cItem_no,rank)=>{
        this.setState({'cItem':chosenItem,'price':itemPrice,'cItem_no':cItem_no,'rank':rank})
        console.log("Field",this.state.cItem,this.state.price,this.state.qty);
    }

    updateBill=()=>{
        console.log("in udatjw");
        fetch('https://billmaster-backend.appspot.com/updateBill',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                item:this.state.cItem,
                quantity:this.state.qty,
                price:this.state.price,
                rest_id:this.props.match.params.rest_id,
                cust_id:this.props.match.params.cust_id,
                item_no:this.state.cItem_no,
            })
        }).then(this.getitems()).then(this.getitems());

        fetch('https://billmaster-backend.appspot.com/updateRank',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                item:this.state.cItem,
                rest_id:this.props.match.params.rest_id,
                item_no:this.state.cItem_no,
                rank:this.state.rank
            })
        });

        console.log("Updated rank",this.state.menu.rank)

    }

    deleteCust=()=>{
        fetch('https://billmaster-backend.appspot.com/deleteCustomer',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                cust_id:this.props.match.params.cust_id
            })
        }).then(this.getitems()).then(this.getitems());
    }


    render(){
    const {onRouteChange}=this.props;
    const filteredMenu=this.state.menu.filter(items=>{
        return items.item.toLowerCase().includes(this.state.search.toLowerCase());
    })

    return(
        <div>
        <Navigation onRouteChange={this.onRouteChange} email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
            <div className="QtyLoop">
                
                
                <div className="cust_id">
                <p className="custId">{this.props.match.params.cust_id} - {this.props.match.params.cust_name} </p>
                </div>


                <p className="blankLine">__</p>
                
                <div className="searchFromMenu">
                    <p className="searchBox"><i className="searchIcon fa fa-search" aria-hidden="true"></i> Search Menu</p>
                </div>

                <div className="searchMenu">
                <input onChange = {this.onItemChange} type="text" class="form-control frmcntrl" placeholder="Search Menu" aria-describedby="basic-addon2"/>
                </div>

                <div className="box">
                    <div className="ScrollBox" style={{ overflow: 'scroll', border: '1px solid orange', height: '200px'}}>

                        <div className="DisplayMenu"  >
                        {
                            filteredMenu.map(items=>(
                            <p key={items.item_no} onClick={this.setField.bind(this,items.item,items.price,items.item_no,items.rank)}>{items.item}-{items.price}</p>
                            )
                            )
                        }
                        </div>
                    </div>
                    <p className="choiceDisplay">Selected- <span className="dispSelected"> {this.state.cItem}</span></p>
                </div>



                <div class="itemQty">
                    <input onChange = {this.onQuantityChange}type="text" class="form-control frmcntrl" placeholder="Quantity" aria-label="Customer Name" aria-describedby="basic-addon2"/>
                    <div class="input-group-append">
                    <button class="btn btn-mdb-color buttn" type="button" onClick = {this.updateBill}><i class="fa fa-check-square-o"></i> Add</button>
                </div>

                <div className="refreshButton">
                    <button className="buttonRef" href="#0" onClick={this.getitems}><i class="fa fa-refresh" aria-hidden="true"></i></button>
                    </div>
                </div>
                        <table>
                                <tr>
                                <th className="Qty">Qty.</th>
                                <th className="item">Item</th>
                                <th className="amt">Amt.</th>
                                </tr>
                                {
                                    this.state.details.map(ritem => (

                                    <tr key={ritem.item_no}>
                                    
                                    <td className="qtyBtn">
                                    <div class="input-group plus-minus-input">
                                    <div class="input-group-button">
                                        <button type="button" class="button hollow circle" data-quantity="plus" onClick={this.IncrementItem.bind(this,ritem.item,ritem.quantity,ritem.item_no,ritem.ser_num)} data-field="quantity">
                                        <i class="fa fa-plus" aria-hidden="true"></i>
                                        </button>
                                    </div>
                                    
                                    <p class="input-group-field" type="number" name="quantity">{this.state.clicks=ritem.quantity}</p>
                                    
                                    <div class="input-group-button">
                                        <button type="button" class="button hollow circle" data-quantity="minus" onClick={this.DecreaseItem.bind(this,ritem.item,ritem.quantity,ritem.item_no,ritem.ser_num)}  data-field="quantity">
                                        <i class="fa fa-minus" aria-hidden="true"></i>
                                        </button>
                                    </div>
                                    </div>
                                    </td>

                                    <td className="item" key={ritem.item_no}>{this.state.item=ritem.item}</td>
                                    <td className="amt">{ritem.price}</td>
                                    <td className="del1"><button className="delbtn button" href="#0" onClick={this.DeleteItem.bind(this,ritem.item,ritem.item_no,ritem.ser_num)} ><i class="fa fa-close"></i></button></td>
                                    </tr>))
                                }
                        </table>
                <div className="buttons">
                    <Link to={"/home/tables/"+this.props.match.params.email+"/"+this.props.match.params.rest_id}><button className="button1" href="#0" onClick={()=>onRouteChange('tables')}><i class="fa fa-arrow-left"></i> Back</button></Link><span>
                    <Link to={'/home/tables/'+this.props.match.params.email+'/updateBill/'+this.props.match.params.rest_id+'/'+this.props.match.params.cust_id+'/'+this.props.match.params.cust_name+'/summary'}><button className="button2" href="#0" onClick={()=>onRouteChange('summary')}><i class="fa fa-check"></i> Submit</button></Link></span>
                </div>
                <div className="empty">
                <p className="blankLine">_</p>
                </div>
            </div>
                <div className="delCust">
                    <Link to={"/home/tables/"+this.props.match.params.email+"/"+this.props.match.params.rest_id}><button className="button2" onClick={this.deleteCust}><i class="fa fa-close"></i> Delete</button></Link>
                </div>

            </div>
        )   
}
    
}

export default QuantityLoop;*/

