import React, { Component } from 'react';
import logo from './logo.svg';
import 'tachyons';
import './App.css';
import Button from './components/Button/Button';
import Navigation from './components/Navigation/Navigation';
import NavigationLogin from './components/Navigation/NavigationLogin';
import Login from './components/Login/Login';
import Login2 from './components/Login/Login';
import NewCustomer from './components/newCustomer/NewCustomer';
import SearchTable from './components/SearchTable/SearchTable';
import {List} from './components/Quantity/List';
import Cards from './components/CardList/Cards';
import QuantityLoop from './components/Quantity/QuantityLoop';
import QuantityMonitor from './components/QuantityMonitor/QuantityMonitor';
import Summary from './components/Summary/Summary';
import SummaryMonitor from './components/SummaryMonitor/SummaryMonitor';
import Home from './components/Home/Home';
import RemoteView from './components/RemoteView/RemoteView';
import Qtybtn from './components/qtybtn/qtybtn';
import { Link, Route, Switch } from 'react-router-dom';
import Empty from './components/Empty/Empty';
import Monitor from './components/Monitor/Monitor';
import Analysis from './components/Analysis/Analysis';
import Orders from './components/Analysis/Orders';
import Customers from './components/Analysis/Customers';
import Online from './components/Analysis/Online';
import Menu from './components/Menu/Menu';
import UpdateMenu from './components/Menu/UpdateMenu';
import CustomerBill from './components/CustomerBill/CustomerBill';
import Register from './components/Register/Register';
import AboutUs from './components/AboutUs/AboutUs';
import TermsAndConditions from './components/TermsAndConditions/TermsAndConditions';


class App extends Component {


  constructor()
  {
    super();
    this.loadCustomer=this.loadCustomer.bind(this);
    this.Days=0;
    this.state=
    {
      route:'signIn',
      isSignedIn:false,
      users:{
            email:'',
            //password:'',
            rest_name:'',
            rest_id:0,
            item:'',
            price:0,
            cust_name:'',
            qty:0,
            amount:0,
            cust_id:0,
            days_left:0
          },
      search:'',
      List:List,
    }
  }


  loadUser=(data)=>{
    this.setState({users:{
            email:data.email,
            rest_id:data.rest_id
    }
  })
    console.log("App.js rest_id",this.state.users.email);
  }

  loadDaysLeft=(data)=>{
    console.log(data);
    this.setState({users:{
      days_left:data
    }})
    this.Days=data;
  }

  loadCustomer=(data)=>{
    this.setState({users:{
      cust_id:data
    }})
    console.log("Apppppp rest_id",this.state.users.rest_id);
    console.log("Apppppppp cust_id",this.state.users.cust_id);
  }


  onRouteChange=(route)=>{
    this.setState({route:route});
    console.log(this.state.route);

  }


onSearch=(event) => 
{
this.setState({search : event.target.value})
}

componentDidMount()
{
  console.log("Ap.jswkmd",this.state.route);
}




  render() {
    const {isSignedIn,route}=this.state;
    return (
      <div className="App">
            <Route exact path="/" render={(props)=><Login loadUser={this.loadUser} onRouteChange={this.onRouteChange} loadDaysLeft={this.loadDaysLeft}/>}/>
            <Route exact path="/aboutUs" render={(props)=><AboutUs/>}/>

            {
              route==='home'?
                <Route exact path="/home/:result/:email" render={(props)=><Home rest_id={this.state.users.rest_id} onRouteChange={this.onRouteChange} email={this.state.users.email} days_left={this.Days} /> }/>
              :
              <Route path="/login" render={(props)=><Login loadUser={this.loadUser} onRouteChange={this.onRouteChange} loadDaysLeft={this.loadDaysLeft}/>}/>
            } 
            <Route exact path="/home/tables/:email/:rest_id" render={(props)=><Cards {...props} rest_id={this.state.users.rest_id} loadCustomer={this.loadCustomer} onRouteChange={this.onRouteChange} />}/> 
            <Route exact path={"/home/tables/:email/updateBill/:rest_id/:cust_id/:cust_name"} render={(props)=><QuantityLoop {...props} cust_id={this.state.users.cust_id} rest_id={this.state.users.rest_id} onRouteChange={this.onRouteChange} />}/>
            <Route exact path={"/home/monitor/:email/updateBill/:rest_id/:cust_id/:cust_name"} render={(props)=><QuantityMonitor {...props} cust_id={this.state.users.cust_id} rest_id={this.state.users.rest_id} onRouteChange={this.onRouteChange} />}/>
            <Route exact path={"/home/tables/:email/updateBill/:rest_id/:cust_id/:cust_name/summary"} render={(props)=><Summary {...props} rest_id={this.state.users.rest_id} cust_id={this.state.users.cust_id} />}/>
            <Route exact path={"/home/monitor/:email/updateBill/:rest_id/:cust_id/:cust_name/summary"} render={(props)=><SummaryMonitor {...props} rest_id={this.state.users.rest_id} cust_id={this.state.users.cust_id} />}/>
            <Route exact path={"/home/monitor/:email/:rest_id"} render={(props)=><Monitor {...props}/>}/>
            <Route exact path={"/home/analysis/:email/:rest_id"} render={(props)=><Analysis {...props}/>}/>
            <Route exact path={"/home/analysis/orders/:email/:rest_id"} render={(props)=><Orders {...props}/>}/>
            <Route exact path={"/home/analysis/online/:email/:rest_id"} render={(props)=><Online {...props}/>}/>
            <Route exact path={"/home/analysis/customers/:email/:rest_id"} render={(props)=><Customers {...props}/>}/>
            <Route exact path={"/home/restaurant/menu/:email/:rest_id"} render={(props)=><Menu {...props}/>}/>
            <Route exact path={"/home/restaurant/menu/update/:email/:rest_id/:item/:item_no/:price"} render={(props)=><UpdateMenu {...props}/>}/>
            <Route exact path={"/customer/bill/:rest_id/:cust_id/:cust_name"} render={(props)=><CustomerBill {...props} rest_id={this.state.users.rest_id} cust_id={this.state.users.cust_id} />}/>
            <Route exact path={"/register"} render={(props)=><Register {...props}/>}/>
            <Route exact path={"/TermsAndConditions"} render={(props)=><TermsAndConditions {...props}/>}/>
      </div>

    );
  }
}

export default App;



