import React from 'react';
import './Analysis.css';
import { Link, Route, Switch } from 'react-router-dom';
import Navigation from '../Navigation/Navigation';

class Orders extends React.Component{

    constructor({props})
    {
        super(props);
        this.c=0;
        this.state={
            'topFive':[],
            'bottomFive':[],
            'allItems':[]
        }

    }

    componentDidMount()
    {
        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/orders/rank/top/descending',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id
            })
        })
        .then(response=>response.json())
        .then(results=>{
            console.log("lsndflas----",results)
            this.setState({'topFive':results})
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/orders/rank/top/ascending',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id
            })
        })
        .then(response=>response.json())
        .then(results=>{
            console.log("lsndflas----",results)
            this.setState({'bottomFive':results})
        })

        fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/analysis/orders/rank/descending',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
                rest_id:this.props.match.params.rest_id
            })
        })
        .then(response=>response.json())
        .then(results=>{
            console.log("lsndflas----",results)
            this.setState({'allItems':results})
        })
    }

    render(){
        return(
            <div>
            <Navigation email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
                <div className="headers">
                  <p className="analysisT">Bill Analysis.</p>
                  <br />
                  <hr />
                  <p className="topFiveT">Top 5 Ordered Items-</p>
                  <table>
                      <tbody>
                        <tr>
                        <th className="itemName">Item</th>
                        <th className="timesOrdered">Times Ordered</th>
                        </tr>
                        {
                            this.state.topFive.map(rank=>(
                            <tr>
                                <td>{rank.item}</td>
                                <td>{rank.rank}</td>
                            </tr>
                            ))
                        }
                    </tbody>
                </table>
                <br />
                <p className="topFiveT">Bottom 5 Ordered Items-</p>
                  <table>
                      <tbody>
                        <tr>
                        <th className="itemName">Item</th>
                        <th className="timesOrdered">Times Ordered</th>
                        </tr>
                        {
                            this.state.bottomFive.map(rank=>(
                            <tr>
                                <td>{rank.item}</td>
                                <td>{rank.rank}</td>
                            </tr>
                            ))
                        }
                    </tbody>
                </table>

                <br />
                <p className="topFiveT">All Items-</p>
                <br />
                <p className="info">Most Ordered - Least Ordered</p>
                  <table>
                      <tbody>
                        <tr>
                        <th className="itemName">Item</th>
                        <th className="timesOrdered">Times Ordered</th>
                        </tr>
                        {
                            this.state.allItems.map(rank=>(
                            <tr>
                                <td>{rank.item}</td>
                                <td>{rank.rank}</td>
                            </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
            <Link to={"/home/analysis/"+this.props.match.params.email+"/"+this.props.match.params.rest_id}><button className="button1"><i class="fa fa-arrow-left"></i> Back</button></Link>
            <br/>
            <br/>
            <br/>
            </div>
            )
            
        }
    }
export default Orders;



