import React from 'react';
import './NewCustomer.css';
//class NewCustomer extends React.Component

class NewCustomer extends React.Component{	
	constructor(props)
	{
		super(props);
		this.state={
			addCustomer:''
		}
	}

	onNameChange =(event)=>
	{
		this.setState({addCustomer: event.target.value})
	}

	onSubmitName=({rest_id})=>{

		fetch('https://pure-scrubland-89366.herokuapp.com/newcustomer',{
			method:'post',
			headers:{'Content-Type': 'application/json'},
			body: JSON.stringify({
				name:this.state.addCustomer,
				rest_id:rest_id
			})
		})
		}

	render()
		{
			return(
				<div class="input-group NewCustomer">
			  		<input onChange={this.onNameChange} type="text" class="form-control frmcntrl" placeholder="Customer Name" aria-label="Customer Name" aria-describedby="basic-addon2" />
			  		<div class="input-group-append">
			    		<button onClick={this.onSubmitName} class="btn btn-mdb-color buttn" type="button">Add Customer</button>
			  		</div>
				</div>
			);
		}
}

export default NewCustomer;


