import React from 'react';
import {Link,Route,Switch} from 'react-router-dom';
import Navigation from '../Navigation/Navigation';
import './Menu.css';

class UpdateMenu extends React.Component
{
	constructor({props})
	{
		super(props);
		this.state={
			'menu':[],
			newItem:'',
			newPrice:0,
		}
	}


	onItemChange=(event)=>{
		this.setState({newItem : event.target.value})
		console.log(this.state.newItem);
	}

	onPriceChange=(event)=>
	{
		this.setState({newPrice:event.target.value})
		console.log(this.state.newPrice);
	}

	updateDetails=()=>
	{
		console.log("updateDetails----",this.state.newItem,this.state.newPrice);
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/updateMenu',{
  			method:'post',
  			headers:{'Content-Type':'application/json'},
  			body:JSON.stringify({
  				item:this.state.newItem,
  				price:this.state.newPrice,
  				rest_id:this.props.match.params.rest_id,
  				item_no:this.props.match.params.item_no
  			})
  		})


	}

	componentDidMount()
	{
		this.setState({'newItem':this.props.match.params.item,'newPrice':this.props.match.params.price})
		console.log("componentDidMount----",this.state.newItem,this.state.newPrice);
	}



	render()
	{
		return(
			<div>
				<Navigation onRouteChange={this.onRouteChange} email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
				<div className="updateItem">
					<div>
						<p className="tupdate"><i class="fa fa-pencil" aria-hidden="true"></i> Update Item.</p>
						<p className="changename">Change name and/or price of dish</p>
					</div>
					
					<div className="olddetails">
						<p className="line">__________________________________________</p>
						<p className="tcurrentVal">Current Values-</p>
						<p className="oldName">{this.props.match.params.item}</p>
						<p className="oldPrice">Rs.{this.props.match.params.price}</p>
					</div>

					<div className="newDetails">
					<p className="tnew">New Name?</p>
					<div className="additembox"><input onChange = {this.onItemChange} type="text" className="form-control frmcntrl" placeholder="New Name" aria-describedby="basic-addon2"/></div>
					<p className="tnewPrice">New Price?</p>
					<div className="addpricebox"><input onChange = {this.onPriceChange} type="text" className="form-control frmcntrl" placeholder="New Price" aria-describedby="basic-addon2"/></div>
					<div className="navButtons">
					<Link to={"/home/restaurant/menu/"+this.props.match.params.email+"/"+this.props.match.params.rest_id}><button className="button1"><i class="fa fa-arrow-left"></i> Back</button></Link>
					<button className="button4" href="#0" onClick={()=>this.updateDetails()}><i class="fa fa-pencil"></i> Update</button>
					</div>
					</div>
				</div>
			</div>
		)
	}
}

export default UpdateMenu;


