import React from 'react';
import './Monitor.css';
import { Link, Route, Switch } from 'react-router-dom';
import Navigation from '../Navigation/Navigation';

class Monitor extends React.Component
{
	constructor({props}){
		super(props);
		this.state={
			'details':[],
			'total':[]
		}
	}

	getItems(){
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/monitor',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
				rest_id:this.props.match.params.rest_id
			})
		})
		.then(response=>response.json())
		.then(results=>this.setState({details:results}))

	}


	test()
	{
		console.log("In tes");
		fetch('http://ec2-13-233-148-213.ap-south-1.compute.amazonaws.com:9000/monitorSum',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
				rest_id:this.props.match.params.rest_id
			})
		})
		.then(response=>response.json())
		.then(results=>this.setState({total:results}))
	}

	componentDidMount(){
		this.getItems();
		this.test();
	}
	
	render(){
		return(	
				<div className="monitorParent">
				<Navigation onRouteChange={this.onRouteChange} email={this.props.match.params.email} rest_id={this.props.match.params.rest_id} />
				
				<div className="monitorChild">
				<p className="monitor">Last 7 Days.</p>
				<br />
				<hr />
				<table className="tablee">
				<thead>
						<tr>
				    	<th>Date</th>
				    	<th>Amount</th>
				    	</tr>
				    	</thead>

				{

					this.state.total.map(rtotal=>(
						<tbody className="rowlink">
						<tr >
						<td className="cust">{rtotal.meal_date}</td>
						<td >{rtotal.sum}</td>
						</tr>
						</tbody> 
					))
				}
				</table>
					
				<p className="monitor">Monitor.</p>
				<br />
				<hr />
				<table className="tablee">
				<thead>
						<tr>
				    	<th className="MDate">Date</th>
				    	<th className="MName">Name</th>
				    	<th className="MAmount">Amount</th>
				    	</tr>
				    	</thead>

				{

					this.state.details.map(ritem=>(
						<tbody className="rowlink">
						<tr >
						<td className="cust"><span className="custId">{ritem.cust_id}</span><br/>{ritem.meal_date}</td>
						<td >{ritem.name}</td>
						<td >{ritem.amount}</td>
						 <td className="nextbnrow"><Link to={"/home/monitor/"+this.props.match.params.email+"/updateBill/"+this.props.match.params.rest_id+"/"+ritem.cust_id+"/"+ritem.name}><button className="nextbtn button"><i class="fa fa-pencil"></i></button></Link></td>
						</tr>
						</tbody> 
					))
				}
				</table>
				</div>
				</div>
			)
	}


}

export default Monitor;


